	module.exports = {"data":{"markdownRemark":{"frontmatter":{"block":"CSS Flexbox"},"html":"<h2>Introduction to the CSS Flexbox Challenges</h2>\n<p>网页的用户界面（User Interface 缩写 UI）包括两个部分。</p>\n<p>第一部分是视觉要素，如色彩、字体和图片等。</p>\n<p>第二部分是这些元素的排列和定位。</p>\n<p>在响应式 Web 设计中，UI 布局必须适配不同尺寸的设备。</p>\n<p>CSS3 引入了 Flexible Box，简称 flexbox（弹性盒子），它特别适合用来创建弹性的页面布局。</p>\n<p>弹性布局以一种可预见的方式排列元素，使其适用于不同尺寸的设备。</p>\n<p>虽然这是个新东西，但所有现代浏览器都已经支持 flexbox。</p>\n<p>本章介绍了如何使用 flexbox 来实现弹性布局。</p>"},"allChallengeNode":{"edges":[{"node":{"fields":{"slug":"/responsive-web-design/css-flexbox/use-display-flex-to-position-two-boxes"},"title":"Use display: flex to Position Two Boxes"}},{"node":{"fields":{"slug":"/responsive-web-design/css-flexbox/add-flex-superpowers-to-the-tweet-embed"},"title":"Add Flex Superpowers to the Tweet Embed"}},{"node":{"fields":{"slug":"/responsive-web-design/css-flexbox/use-the-flex-direction-property-to-make-a-row"},"title":"Use the flex-direction Property to Make a Row"}},{"node":{"fields":{"slug":"/responsive-web-design/css-flexbox/apply-the-flex-direction-property-to-create-rows-in-the-tweet-embed"},"title":"Apply the flex-direction Property to Create Rows in the Tweet Embed"}},{"node":{"fields":{"slug":"/responsive-web-design/css-flexbox/use-the-flex-direction-property-to-make-a-column"},"title":"Use the flex-direction Property to Make a Column"}},{"node":{"fields":{"slug":"/responsive-web-design/css-flexbox/apply-the-flex-direction-property-to-create-a-column-in-the-tweet-embed"},"title":"Apply the flex-direction Property to Create a Column in the Tweet Embed"}},{"node":{"fields":{"slug":"/responsive-web-design/css-flexbox/align-elements-using-the-justify-content-property"},"title":"Align Elements Using the justify-content Property"}},{"node":{"fields":{"slug":"/responsive-web-design/css-flexbox/use-the-justify-content-property-in-the-tweet-embed"},"title":"Use the justify-content Property in the Tweet Embed"}},{"node":{"fields":{"slug":"/responsive-web-design/css-flexbox/align-elements-using-the-align-items-property"},"title":"Align Elements Using the align-items Property"}},{"node":{"fields":{"slug":"/responsive-web-design/css-flexbox/use-the-align-items-property-in-the-tweet-embed"},"title":"Use the align-items Property in the Tweet Embed"}},{"node":{"fields":{"slug":"/responsive-web-design/css-flexbox/use-the-flex-wrap-property-to-wrap-a-row-or-column"},"title":"Use the flex-wrap Property to Wrap a Row or Column"}},{"node":{"fields":{"slug":"/responsive-web-design/css-flexbox/use-the-flex-shrink-property-to-shrink-items"},"title":"Use the flex-shrink Property to Shrink Items"}},{"node":{"fields":{"slug":"/responsive-web-design/css-flexbox/use-the-flex-grow-property-to-expand-items"},"title":"Use the flex-grow Property to Expand Items"}},{"node":{"fields":{"slug":"/responsive-web-design/css-flexbox/use-the-flex-basis-property-to-set-the-initial-size-of-an-item"},"title":"Use the flex-basis Property to Set the Initial Size of an Item"}},{"node":{"fields":{"slug":"/responsive-web-design/css-flexbox/use-the-flex-shorthand-property"},"title":"Use the flex Shorthand Property"}},{"node":{"fields":{"slug":"/responsive-web-design/css-flexbox/use-the-order-property-to-rearrange-items"},"title":"Use the order Property to Rearrange Items"}},{"node":{"fields":{"slug":"/responsive-web-design/css-flexbox/use-the-align-self-property"},"title":"Use the align-self Property"}}]}},"pathContext":{"block":"css-flexbox","slug":"/responsive-web-design/css-flexbox"}}

